
import React from 'react';
import App from './App';
import { createBrowserRouter } from 'react-router-dom';
import TicTacToe from './TicTacToe';
// import createBrowserRouter from "react-router-dom";
const router = createBrowserRouter([
    {
        path: "/",
        Component: App,
        children: [
            {
                path: "tic-tac-toe",
                Component: TicTacToe,
            },
        ],
    },
    {
        path: "/*",
        element: <p>This is not a valid page.</p>
    }
]);

export default router;